// Import necessary dependencies
import React, { useEffect, useRef, useState } from "react";
import styles from "./index.module.css";
import { useTranslation } from "react-i18next";
import Button from "../../components/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlay, faPause } from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import { AppDispatch, RootState } from "../../store";
import { useDispatch, useSelector } from "react-redux";
import { fetchPreviewUrl, getRecommend, search, setRecommend } from "../../store/videoSlice";
import { Product, SearchParam, Tag } from "../../type/videoType";
import defaultPng from "../../images/qc-default-img.jpg";
import Loading from "../../components/Loading";
import { set } from "lodash";
import { getRandomNumber, getUrl } from "../../utils/helper";
import { preProcessFile } from "typescript";
import { getUnit } from "@mui/material/styles/cssUtils";
import { getFontClass, switchName } from "../../utils/helper";
import { Language } from "@mui/icons-material";
import { setRecommend2Pay, setRecommend2Video } from "../../store/analyticsSlice";

// Define interfaces for props
interface TagsProps {
  tags: Tag[];
}

interface VideoProps {
  reference: string;
  id: string;
  msIds: string
  title: string;
  uuid?: string;
  description: string;
  tags: Tag[];
  index: number;
  length: number;
  pageNum: number;
  currentIndex: number;
  setCurrentIndex: (index: number) => void;
  hasInteracted: boolean;
}

interface VideoData {
  reference: string;
  id: number;
  title: string;
  uuid?: string;
  description: string;
  tags: string[];
  index: number;
}

// Tags component
const Tags: React.FC<TagsProps> = ({ tags }) => {
  const navigate = useNavigate();
  const handleTagsClick = (searchValue: string) => {
    navigate(`/app/search`, { state: { kw: searchValue } });
  };
  const language = useSelector((state: RootState) => state.init.language);

  return (
    <div className="flex-wrap flex items-end space-x-2 space-y-2">
      {tags.map((tag, index) => (
        <span
          key={index}
          className="bg-background rounded-full text-primary-active text-xs p-2 cursor-pointer"
          onClick={() => handleTagsClick(tag.tag_name)}
        >
          {switchName(tag, language)}
        </span>
      ))}
    </div>
  );
};

// Video component
const Video: React.FC<VideoProps> = ({
  reference,
  id,
  msIds,
  title,
  uuid,
  description,
  tags,
  index,
  currentIndex,
  setCurrentIndex,
  hasInteracted
}) => {
  const { t } = useTranslation();
  const [isPlaying, setIsPlaying] = useState<boolean>(true);
  const [isVisible, setIsVisible] = useState<boolean>(false);
  const [isControlVisible, setIsControlVisible] = useState<boolean>(false);
  const [isVideoLoaded, setIsVideoLoaded] = useState<boolean>(false);
  const videoRef = useRef<HTMLVideoElement>(null);
  const placeholderRef = useRef<HTMLDivElement>(null);
  const dispatch: AppDispatch = useDispatch();
  const video = videoRef.current;
  let cancelMutedTimeout: ReturnType<typeof setTimeout> | null = null;

  useEffect(() => {
    const video = videoRef.current;
    if (!video) return;
  
    if (index === currentIndex) {
      if (hasInteracted && isVideoLoaded) {
        video.muted = false;
        video.play().catch((err) => {
          console.warn("播放失敗（取消靜音）:", err);
        });
      } else {
        video.muted = true;
        video.play().catch((err) => {
          console.warn("播放失敗（靜音播放）:", err);
        });
      }
    } else {
      video.pause();
    }
  }, [currentIndex, isVideoLoaded, hasInteracted]);
  
  const language = useSelector((state: RootState) => state.init.language);


  const navigate = useNavigate();

  const togglePlayPause = () => {
    setIsPlaying((prev) => {
      if (prev) {
        videoRef.current?.pause();
      } else {
        videoRef.current?.play();
      }
      return !prev;
    });

    setIsControlVisible(true);
    setTimeout(() => setIsControlVisible(false), 500);
  };

  useEffect(() => {
    if (!placeholderRef.current) return;

    const observer = new IntersectionObserver(
      (entries) => {
        const target = entries[0];
        if (target.isIntersecting) {
          setCurrentIndex(index);
        }
      },
      {
        threshold: 0.5,
        rootMargin: "100px",
      }
    );

    observer.observe(placeholderRef.current);

    return () => {
      if (placeholderRef.current) {
        observer.unobserve(placeholderRef.current);
      }
    };
  }, []);

  return (
    <div className={styles.video} ref={placeholderRef}>
      <div className="text-orange-200 text-xl self-start m-4 bg-red-500 p-2 rounded-lg">
        {reference}
      </div>
      <div className={`${styles.video__wrapper} aspect-[1.49]`} onClick={togglePlayPause}>
        <div
          className={`${styles.video__placeholder}`}
          style={{
            display: isVideoLoaded ? "none" : "block",
            width: "100%", // 或者設定固定寬度
          }}
        >
          <img src={defaultPng} className="w-full h-full object-cover" />
        </div>
        
            <video
              className={`${styles.video__player} ${
                isVideoLoaded && index === currentIndex ? "" : "hidden"
              } w-full h-full object-cover`}
              ref={videoRef}
              muted
              autoPlay
              loop
              playsInline
              onLoadedData={() => setIsVideoLoaded(true)}
            >
              {uuid && index === currentIndex && <source src={`https://iqqtv.net/subpage/getMvPreview.php?uuid=${uuid}`} type="video/mp4" />}
            </video>
      
        <div
          className={`${styles.video__controls} ${
            isControlVisible ? styles.show : styles.hide
          }`}
        >
          <FontAwesomeIcon icon={isPlaying ? faPause : faPlay} />
        </div>
      </div>
      <div className="m-4 space-y-4">
        <p className={`text-sm text-gray-300 ${styles.description}`}>
          {description}
        </p>
        <div className={styles.tags}>
          <Tags tags={tags} />
        </div>

        <Button
          className={`${getFontClass(language)} button-full active py-3`}
          onClick={() => {
            dispatch(setRecommend2Pay("video"));
            dispatch(setRecommend2Video("video"));
            navigate(`/app/Video?uuid=${uuid}`);
          }}
        >
          {t("觀看完整影片")}
        </Button>
      </div>
    </div>
  );
};

const Recommendation: React.FC = () => {
  const { t } = useTranslation();
  const [loading, setLoading] = useState(true);
  const [hasInteracted, setHasInteracted] = useState(false);
  const [currentIndex, setCurrentIndex] = useState(0);
  const dispatch: AppDispatch = useDispatch();
  const searchVideoData = useSelector(
    (state: RootState) => state.video.recommend
  );
  const [data, setData] = useState<Product[]>([]);
  const observerRef = useRef<HTMLDivElement | null>(null);
  const pageNum = getRandomNumber();
  const language = useSelector((state: RootState) => state.init.language);

  const handleLoadMore = () => {
    const config: SearchParam = {
      s_type: "event",
      s_tid: "17",
      num: Number(searchVideoData?.pageNum) + 1,
    };
    dispatch(getRecommend(config));
  };

  useEffect(() => {
    const config: SearchParam = {
      s_type: "event",
      s_tid: "17",
      num: Number(pageNum)
    };
    dispatch(getRecommend(config));
  }, [dispatch]);

  useEffect(() => {
    if (searchVideoData) {
      if (Number(searchVideoData.pageNum) === pageNum) {
        setData(searchVideoData?.products);
      } else {
        setData((prevData) => [...prevData, ...searchVideoData?.products]);
      }
    }
  }, [searchVideoData]);

  useEffect(() => {
    if (data.length !== 0) {
      setLoading(false);
    }
  }, [data])

  useEffect(() => {
    return () => {
      setLoading(true);
      dispatch(setRecommend(null));
      setData([]);
    }
  }, [])

  useEffect(() => {
    if (!observerRef.current) return;

    const observer = new IntersectionObserver(
      (entries) => {
        const target = entries[0];
        if (target.isIntersecting) {
          handleLoadMore();
        }
      },
      {
        root: document.getElementById("recommendationContainer"),
        threshold: 0.5,
      }
    );

    observer.observe(observerRef.current);

    if (Number(searchVideoData?.pageNum) === Number(searchVideoData?.pageTotals)) {
      observer.unobserve(observerRef.current);
    }

    return () => {
      if (observerRef.current) {
        observer.unobserve(observerRef.current);
      }
    };
  }, [searchVideoData]);

  return (
    <div>
      {loading ? (
        <div>
          <Loading />
        </div>
      ) : (
        <div className={styles.container} id="recommendationContainer" onClick={() => setHasInteracted(true)}>
          {data.map((video, index) => (
            <Video
              key={index}
              reference={video.barcode}
              title={video.name}
              description={switchName(video, language)}
              tags={video.tag_ids}
              id={video.product_id}
              msIds={video.ms_ids}
              uuid={video.uuid}
              index={index}
              length={data.length}
              pageNum={searchVideoData?.pageNum ?? pageNum}
              currentIndex={currentIndex}
              setCurrentIndex={setCurrentIndex}
              hasInteracted={hasInteracted}
            />
          ))}
          <div ref={observerRef} className="observer-target h-8"></div>
        </div>
      )}
    </div>
  );
};

export default Recommendation;
